<template>
	<!-----------------------------------Header wrap---------------------------------->
	<HeaderComponent />
	
	<!--------------------------Company Icon & Search Bar wrap------------------------>
	<SearchComponent />
	
	<!---------------------------------Navigator wrap-------------------------------->
	<NavComponent />


	<!--------------------------------Products catalog--------------------------->
	<div class="productsArea">
        <SideProductsCatlogComponent />
        
		<div class="customerInfoWrap" >		
            <table class="infoTable" align="center">
                <tr>
                    <td>用户名：</td>
                    <!-- When user is not login -->
                    <td  v-if="checkLoginData.isNotLogin">请登录</td>
                    <!-- When user is login & has no address -->
                    <td v-else-if="!addrData.hasAddress && !checkLoginData.isNotLogin">请添加收货地址</td>
                    <!-- :冒号表示js表达式，:key="item.username表示用item里面的username作为key值" -->
                    <td v-else>{{addrData.getAddrData.username}}</td>
                </tr>						         
                <tr>
                    <td>省份：</td>
                    <td  v-if="checkLoginData.isNotLogin">请登录</td>                    
                    <td v-else-if="!addrData.hasAddress && !checkLoginData.isNotLogin">......</td>
                    <td v-else >{{addrData.getAddrData.province}}</td>
                </tr>
                <tr>
                    <td>城市：</td>
                    <td  v-if="checkLoginData.isNotLogin">请登录</td>
                    <td v-else-if="!addrData.hasAddress && !checkLoginData.isNotLogin">......</td>
                    <td v-else >{{addrData.getAddrData.city}}</td>
                </tr>
                <tr>
                    <td>区名：</td>
                    <td  v-if="checkLoginData.isNotLogin">请登录</td>
                    <td v-else-if="!addrData.hasAddress && !checkLoginData.isNotLogin">......</td>
                    <td v-else >{{addrData.getAddrData.suburb}}</td>
                </tr>
                <tr>
                    <td>街道：</td>
                    <td  v-if="checkLoginData.isNotLogin">请登录</td>
                    <td v-else-if="!addrData.hasAddress && !checkLoginData.isNotLogin">......</td>
                    <td v-else >{{addrData.getAddrData.street}}</td>
                </tr>
                <tr>
                    <td>门牌号：</td>
                    <td  v-if="checkLoginData.isNotLogin">请登录</td>
                    <td v-else-if="!addrData.hasAddress && !checkLoginData.isNotLogin">......</td>
                    <td v-else >{{addrData.getAddrData.houseNum}}</td>
                </tr>
                <tr>
                    <td>收货人：</td>
                    <td  v-if="checkLoginData.isNotLogin">请登录</td>
                    <td v-else-if="!addrData.hasAddress && !checkLoginData.isNotLogin">......</td>
                    <td v-else >{{addrData.getAddrData.name}}</td>
                </tr>
                <tr>
                    <td>电话：</td>
                    <td  v-if="checkLoginData.isNotLogin">请登录</td>
                    <td v-else-if="!addrData.hasAddress && !checkLoginData.isNotLogin">......</td>
                    <!-- <td v-else v-for="item in addressInfo" :key="item.phone">{{item.phone}}</td> -->
                    <td v-else >{{addrData.getAddrData.phone}}</td>
                </tr>
                <tr>
                    <td>VIP: </td>
                    <td  v-if="checkLoginData.isNotLogin">请登录</td>
                    <td v-else-if="!addrData.hasAddress && !checkLoginData.isNotLogin">......</td>
                    <td v-else-if="addrData.getAddrData.vip">是</td>
                    <td v-else-if="!addrData.getAddrData.vip">否</td>
                </tr>
            </table>
            <button class="changeInfo" 
                v-if=" !checkLoginData.isNotLogin && addrData.hasAddress "
                @click="routerLinkToUpdateAddress"
            >
                更改收货信息
            </button>
            <button class="createAddr" 
                v-if=" !checkLoginData.isNotLogin && !addrData.hasAddress "
                @click="routerLinkToAddressSetting"
            >
                添加收货地址
            </button>
            
        </div>
    </div>
</template>

<script>

    // import { reactive } from '@vue/reactivity'
    import {useRouter} from 'vue-router'
	import HeaderComponent from '../../components/HeaderComponent.vue'
	import SearchComponent from '../../components/SearchComponent.vue'
	import NavComponent from '../../components/NavComponent.vue'
	import SideProductsCatlogComponent from '@/components/SideProductsCatlogComponent.vue'
    // import {get} from '../../utils/reqEncap'
    import {checkIsLogin} from '../../midwares/checkIsLogin'
    import {getUserInfo} from '../../midwares/checkHasAddress'


    const handleLinkToUpdateEvent = () => {
        const router = useRouter()
        const routerLinkToUpdateAddress = () => {
            router.push({name: 'UpdateAddr'})
        }            
        const routerLinkToAddressSetting = () => {
            router.push({name: 'CreateAddress'})
        }

        return { routerLinkToUpdateAddress, routerLinkToAddressSetting }
    }

    export default {
        name: 'CustomerInfo',
        components: {HeaderComponent, SearchComponent, NavComponent, SideProductsCatlogComponent}, 
        setup() {
            
            const router = useRouter()
            const {checkLoginData} = checkIsLogin()
            const {getCustomerInfo, addrData} = getUserInfo()
            // If login, the function getCustomerInfo() will be executed to get address info
            // & 
            if(checkLoginData.isNotLogin == false){
                getCustomerInfo()
            }

            const { routerLinkToUpdateAddress, routerLinkToAddressSetting } = handleLinkToUpdateEvent()
                        
            return {checkLoginData, addrData, routerLinkToUpdateAddress, routerLinkToAddressSetting}
        }
    }
</script>

<style lang="scss">

    // 客户信息的CSS在style文件夹中可以找到

</style>